.top {
  border-bottom: 1px solid #ececec;
  background: $bg;
  &__in {
    display: table;
    width: 100%; }
  &__left,
  &__right {
    display: table-cell;
    vertical-align: middle;
    height: 50px; }
  &__left {
    @include r($xs) {
      display: none; } }
  &__right {
    text-align: right;
    @include r($xs) {
      height: 35px; } }
  &__nav {
    font-size: 0;
    margin: 0 -1rem;
    &-item {
      display: inline-block;
      vertical-align: middle; }
    &-link {
      display: block;
      padding: .5rem 1rem;
      color: $text;
      font-size: 14px;
      font-weight: 600;
      transition: color .3s;
      &:hover {
        color: $link; } } }
  &__contacts {
    display: inline-block;
    margin-right: -5px;
    vertical-align: middle;
    @include r($xs) {
      display: table;
      width: calc(100% + 10px);
      margin: 0 -5px;
      table-layout: fixed; }
    &-item {
      display: block;
      line-height: 1;
      @include r($xs) {
        display: table-cell;
        vertical-align: middle;
        text-align: left;
        &:last-child {
          text-align: right; } } }

    &-link {
      display: inline-block;
      padding: .3rem .5rem;
      color: $text;
      font-size: 12px;
      font-weight: 400;
      transition: color .3s;
      &:hover {
        color: $link; } } } }
