.header {
  position: relative;
  border-bottom: 1px solid #ececec;
  height: 80px;
  z-index: 10;
  @include r($xs) {
    height: 60px; }
  &__wrap {
    @include r(1024) {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: #fff; } }
  &__in {
    display: table;
    width: 100%; }
  &__left,
  &__right {
    display: table-cell;
    vertical-align: middle;
    height: 80px;
    @include r($xs) {
      height: 60px; } }
  &__right {
    text-align: right; }
  .cash,
  .user-link {
    margin-left: 10px; }
  .cash {
    @include r($xs) {
      margin-left: 5px; } }

  .btn-add {
    margin-left: 47px;
    @include r($xs) {
      padding: 12px 16px;
      .fa {
        margin-right: 0; }
      span {
        display: none; } }
    @include r($md) {
      margin-left: 20px; } } }

.logo {
  display: table;
  vertical-align: middle;
  table-layout: fixed;
  width: 370px;
  @include r($md) {
    width: auto; }
  &__img {
    display: table-cell;
    border-right: 3px solid #f68a1f;
    width: 125px;
    @include r($md) {
      width: auto;
      border: 0; }
    @include r($xs) {
      width: 70px; }
    img {
      @include r($xs) {
        display: block;
        width: 100%;
        height: auto; } } }
  &__text {
    display: table-cell;
    vertical-align: middle;
    padding-left: 25px;
    color: $text;
    font-size: 13px;
    font-weight: 400;
    line-height: 1.54;
    @include r($md) {
      display: none; } } }

.link-icon {
  display: inline-block;
  vertical-align: middle;
  padding: 5px 7px;
  font-size: 24px;
  font-weight: 400;
  @include r($xs) {
    font-size: 20px; }
  .fa {
    color: #e5e5e5;
    transition: color .3s; }
  &:hover {
    .fa-star {
      color: $mainDark; }
    .fa-comments {
      color: $link; } } }

.cash {
  display: inline-block;
  vertical-align: middle;
  max-width: 105px;
  line-height: 1;
  padding: .5rem 1.3rem;
  color: $text;
  font-size: 14px;
  font-weight: 600;
  transition: color .3s;
  @include text-overflow;
  @include r($md) {
    padding: .5rem; }
  @include r($xs) {
    color: #e5e5e5; }
  &:hover {
    color: $link; }
  .fa {
    @include r($xs) {
      font-size: 20px; } }
  span {
    @include r($xs) {
      display: none; } } }

.user-link {
  display: inline-block;
  vertical-align: middle;
  max-width: 200px;
  @include text-overflow;
  .fa {
    display: none;
    font-size: 24px;
    color: #e5e5e5;
    transition: color .3s;
    &:hover {
      color: $link; }
    @include r($md) {
      display: block; }
    @include r($xs) {
      font-size: 20px; } }
  .link {
    span {
      @include r($md) {
        display: none; } } } }

